@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,700);
html, body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-size: 100%;
  background-position: bottom;
  min-height: 100%;
  width: 100%;

}


.logo {
  margin-left: 15px;
}
.avatar{
  box-sizing: border-box;
}

.navbar{
  padding: .8rem;
}

.carousel-inner img{
  width: 100%;
  height: 100%;
}

.carousel-caption{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.carousel-caption h1{
  font-size: 500%;
  text-transform: uppercase;
  text-shadow: 1px 1px 15px #000;
}

.carousel-caption h3{
  font-size: 300%;
  font-weight: 500;
  text-shadow: 1px 1px 10px #000;
  padding-bottom: 1rem;
}

.btn-primary{
  background-color: #6648b1;
  border: 1px solid #6648b1;
}
.btn-primary:hover {
  background-color: #563d7c;
  border: 1px solid #563d7c;
}

.jumbotron{
  padding: 1rem;
  border-radius: 0;
}

.padding{
  padding-bottom: 2rem;
}
.welcome {
  /* width: 75%; */
  margin: 0 auto;
  padding-top: 2rem;
}
.welcome hr{
  border-top: 2px solid #b4b4b4;
  width: 95%;
  margin-top: 3rem;
  margin-bottom: 1rem;
}
.fa-code{
  color: #e54d26;
}
.fa-bold{
  color: #563d7c;
}
.fa-css3{
  color: #2163af;
}
.fa-code, .fa-bold, .fa-css3{
  font-size: 4em;
  margin: 1rem;
}
.project-title{
  margin-top: 15px;
}

.constainer{
  width: 100%;
  min-height: 100%;
}

.profile-section{
  background-color:  rgb(250, 246, 246);
  min-height: 100%;
  border-right:black 2px;
}
.profile-image{
  margin-top: 30px;
  width: 200px;
  border-radius: 16em;
}

.content-section {
  background-color: rgb(250, 246, 246);
}

.name-header{
  margin-top: 10px;
  font-size: 32px;
  color: cornflowerblue;
}

/* .background-img{
  background-image: url(/src/assets/img/github_image.png);
} */

form {
  padding: 20px;
  margin-top: 60px;
}

form button, form h5{
  margin: 20px, 0;
}

.login-container{
  align-content: center;
}

.avatar-pic {
  width: 250px;
  height: 100;
  }

.about-container{
  background-color: rgb(250, 246, 246);
  padding: 15px 15px 15px 15px;
}

.page-footer{
  position: relative;
  width: 100%;
  bottom: 0;
}



.nav-icons{
  margin-top: 5px;
}

input[type=text]:not(.browser-default):focus:not([readonly]),
input[type=email]:not(.browser-default):focus:not([readonly]),
input[type=password]:not(.browser-default):focus:not([readonly]),
textarea.materialize-textarea:focus:not([readonly]){
  border-color: #ec407a;
  box-shadow: none;
}

input[type=text]:not(.browser-default):focus:not([readonly]) + label,
textarea.materialize-textarea:focus:not([readonly]) + label,
input[type=email]:not(.browser-default):focus:not([readonly]) + label,
input[type=password]:not(.browser-default):focus:not([readonly]) + label{
  color: #ec407a !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
} 
.navbar-collapse{
  margin-top: 0px !important;
}

/*---Media Queries --*/
@media (max-width: 1128px){
  .brand-logo{
    left: 5px;
    font-size: 21px !important;
  }
}
@media (max-width: 992px) {
  /* .profile-section{
    display: none;
  } */
}
@media (max-width: 768px) {
  .carousel-caption{
    top: 45%;
  }
  .carousel-caption h1{
    font-size: 350%;
  }
  
  .carousel-caption h3{
    font-size: 140%;
  }
  .carousel-caption .btn{
    font-size: 95%;
    padding: 8px 14px;
  }

  .display-4{
    font-size: 200%;
  }

  .nav .navbar-nav {
    margin-top: 20px !important;
    width: 100% !important;
    /* background-color: white; */
    background-color: #000;
  }
  /* .profile-section{
    display: none;
  } */
}

/**
* Profile Page CSS
*/

.profile-page{
  background-image: url(/static/media/mac.b3dc083c.png);
}

.profile-section{
  top: 100px;
}

.social a {
  font-size: 4.5em;
  padding: 3rem;
}

a .material-icons {
  vertical-align: middle;
}

.fixed-top {
  position: fixed;
  z-index: 1030;
  left: 0;
  right: 0;
}

.profile-page .page-header {
  margin-top: -50px;
  height: 380px;
  background-position:center;
}

.page-header {
  height: 100vh;
  background-size: cover;
  margin: 0;
  padding: 0;
  border: 0;
  display: flex;
  align-items: center;
}

.header-filter:after, .header-filter:before {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  display: block;
  left: 0;
  top: 0;
  content: "";
}

.header-filter::before {
  background: rgba(0,0,0,.5);
}

.main-raised {
  margin: -60px 30px 0;
  border-radius: 6px;
  box-shadow: 0 16px 24px 2px rgba(0,0,0,.14), 0 6px 30px 5px rgba(0,0,0,.12), 0 8px 10px -5px rgba(0,0,0,.2);
}

.main {
  background: #FFF;
  position: relative;
  z-index: 3;
}

.profile-page .profile {
  text-align: center;
}

.profile-page .profile img {
  max-width: 160px;
  width: 100%;
  margin: 0 auto;
  transform: translate3d(0,-50%,0);
}

.img-raised {
  box-shadow: 0 5px 15px -8px rgba(0,0,0,.24), 0 8px 10px -5px rgba(0,0,0,.2);
}

.rounded-circle {
  border-radius: 50%!important;
}

.img-fluid, .img-thumbnail {
  max-width: 100%;
  height: auto;
}

.title {
  margin-top: 30px;
  margin-bottom: 25px;
  min-height: 32px;
  color: #3C4858;
  font-weight: 700;
  font-family: "Roboto Slab","Times New Roman",serif;
}

.profile-page .description {
  margin: 1.071rem auto 0;
  max-width: 600px;
  color: #999;
  font-weight: 300;
}

p {
  font-size: 14px;
  margin: 0 0 10px;
}

.profile-page .profile-tabs {
  margin-top: 4.284rem;
}

.profile-page .profile .name{
  margin-top: -80px;
}

img.rounded {
  border-radius: 6px!important;
}

/* End of Profile Page CSS */






@media (max-width: 576px) {
  .brand-logo{
    font-size: 19px !important;
  }
  .display-4{
    font-size: 160%;
  }
  .carousel-caption{
    top: 40%;
  }
  .carousel-caption h1{
    font-size: 250%;
  }
  
  .carousel-caption h3{
    font-size: 110%;
    font-weight: 400;
    padding-bottom: 2rem;
  }
  .carousel-caption .btn{
    font-size: 90%;
    padding: 4px 8px;
  }
  .carousel-indicators{
    display: none;
  }

  .profile-section{
    display: none;
  }
}


/*---Firefox Bug Fix --*/
.carousel-item {
transition: transform 0.5s ease;
-webkit-backface-visibility: visible;
backface-visibility: visible;
}
/*--- Fixed Background Image --*/
.figure {
position: relative;
width: 100%;
height: 60%;
margin: 0!important;
}
.fixed-wrap {
clip: rect(0, auto, auto, 0);
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
}
#fixed {
background-image: url(/static/media/mac.b3dc083c.png);
position: fixed;
display: block;
top: 0;
left: 0;
width: 100%;
height: 100%;
background-size: cover;
background-position: center center;
transform: translateZ(0);
will-change: transform;
}
/*--- Bootstrap Padding Fix --*/
[class*="col-"] {
  padding: 1rem;
}





